.mainNavbar{
    padding: 0;
}
.menuToggler.btn {
    margin-right: 1rem;
    background-color: transparent !important;
    border: none;
    color: #323131 !important;
    font-weight: 800;
    text-transform: uppercase;
    font-family: 'Montserrat', sans-serif;
    outline: none !important;
    box-shadow: 0 0 0 !important;
    border-radius: 0 !important;
    padding: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.menuToggler .bar {
    position: relative;
    display: inline-block;
    width: 2px;
    background-color: #000;
    height: 21px;
    margin-right: 0.75rem;
}
.menuToggler .bar::after, .menuToggler .bar::before {
    width: 2px;
    background-color: #000;
    content: "";
    margin: auto;
    height: 100%;
    position: absolute;
    top: 0;
    bottom: 0;
}
.menuToggler .bar::after{
    right: -5px;
}
.menuToggler .bar::before{
    left: -5px;
}
