#flyoutMenu {
    width: 100vw;
    height: 100vh;
    background-color: #FFE600;
    position: fixed;
    top: 0;
    left: 0;
    -webkit-transition: -webkit-transform 0.8s cubic-bezier(0, .52, 0, 1);
    transition: -webkit-transform 0.8s cubic-bezier(0, .52, 0, 1);
    transition: transform 0.8s cubic-bezier(0, .52, 0, 1);
    transition: transform 0.8s cubic-bezier(0, .52, 0, 1), -webkit-transform 0.8s cubic-bezier(0, .52, 0, 1);
    overflow: scroll;
    z-index: 1000;
    -webkit-transition: transform 0.8s cubic-bezier(0, .52, 0, 1);
    -moz-transition: transform 0.8s cubic-bezier(0, .52, 0, 1);
    -ms-transition: transform 0.8s cubic-bezier(0, .52, 0, 1);
    -o-transition: transform 0.8s cubic-bezier(0, .52, 0, 1);
    -webkit-transform: translate3d(-100vw, 0, 0);
            transform: translate3d(-100vw, 0, 0);
}
.topBarWrap {
    background-color: #fff;
    padding: 1rem 1rem;
}
.closeMenu .closeBtn {
    position: relative;
    background-color: #929292;
    border-radius: 2px;
    width: 3px;
    height: 20px;
    margin-right: 1rem;
    -webkit-transform: rotate(135deg);
            transform: rotate(135deg);
}
.closeMenu .closeBtn::after {
    background-color: #929292;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    content: "";
    width: 3px;
    height: 100%;
    position: absolute;
    border-radius: 3px;
    left: 0px;
    top: 0px;
}
#flyoutMenu.hide {
  -webkit-transform: translate3d(-100vw, 0, 0);
          transform: translate3d(-100vw, 0, 0);
}
#flyoutMenu.show {
  -webkit-transform: translate3d(0vw, 0, 0);
          transform: translate3d(0vw, 0, 0);
  overflow: hidden;
}
#flyoutMenu .menuContWrap {
    background-color: #000;
    width: 100%;
    height: calc(100vh - 56px);
}
